.navbar{
    width: 60vw;
    display: flex;
    margin-left: 20vw;
}
.image{
    width: 120px;
    height: 120px;
}
.imageDark{
    width: 170px;
    height: 120px;
}
button{
    width: 90px;
    border: none;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}
button:hover{
    opacity: 0.4;
}
.nav-darkLink button{
    background-color: black;
    color: white;
}
.nav-links button{
    background-color: white;
    color: black;
}
.bulbOn, .bulbOff{
    width: 90px;
    object-fit: cover;
}
.nav-links, .nav-darkLink{
    display: flex;
    width: 60vw;
    justify-content: end;
    align-items: center;
}
.skill, .blog, .social{
    width: 70px;
}